
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import nearShop from '../../components/goods/nearShop'
import comment from '../../components/comment/index'
import share from '../../components/share/index'
export default {
name: 'vcard',
components: {
XButton,
Box,
nearShop,
Swiper,
Flexbox,
FlexboxItem,
comment,
share
},
data () {
return {
pro_id: 0,
pro_info : {
property: [],
pro_sku: []
},
gutter: 0,
sku_code_list: [],
select_property: {},
pro_code: '',
sku_pro: {},
nearShop: {},
nearShopList: [],
commentInfo: {},
demo02_index: 0,
lat: 0,
lng: 0,
f: false,
t: true,
demo01_list: [{
url: 'javascript:',
img: 'https://static.vux.li/demo/1.jpg',
title: 'fua'
}, {
url: 'javascript:',
img: 'https://static.vux.li/demo/2.jpg',
title: ''
}, {
url: 'javascript:',
img: 'https://static.vux.li/demo/5.jpg',
title: '',
fallbackImg: 'https://static.vux.li/demo/3.jpg'
}]
}
},
mounted: function (e) {
var pro_id = this.$route.params.id
this.pro_id = pro_id
console.log(pro_id)
var self = this
this.xarpost('Product/getProduct', {'pro_id': this.pro_id}).then(function(d){
let {data, error, message} = d
data.property.map(function(item){
item.is_select = 0
item.s_index = 0
item.value.map(function(i, elem) {
i.select = 0
})
});
self.pro_info = data
self.allSkucode()
console.log(data)
}, function(d){
this.showtoast('')
})

self.getNearShop();
self.fetchComment();
},
beforeUpdate: function(){
this.setWxShare('', '', 'http://xcxcgj.gotomore.cn/index.html#/vcard/'+this.pro_id)
},
computed: {
propertySelect: function(){
var str = '';
var noSelect = [], isSelect = []
this.pro_info.property.map(function(item, elem) {
if(!item.is_select){
noSelect.push(item)
}else{
isSelect.push(item)
}
})
if(noSelect.length > 0){
str = ''
noSelect.map(function(item, elem) {
str += ' ' + item.property_name
})
}else{
str = ''
isSelect.map(function(item, elem) {
str += ' "' + item.value[item.s_index].pv_value + '"'
})
}
return str;
},
createSkuCode: function(){
var str = '';
var noSelect = [], isSelect = []
var pro_code = this.pro_id
this.pro_code = ''
this.pro_info.property.map(function(item, elem) {
if(!item.is_select){
noSelect.push(item)
}else{
isSelect.push(item)
}
})
if(!noSelect.length > 0){
str = ''
console.log(isSelect, 'isSelect')
isSelect.map(function(i, e) {
pro_code += i.value[i.s_index].pv_id
})
this.pro_code = pro_code

}
console.log(pro_code, 'aaaa')
return '';
},
getSkuProBySkucode: function(){
var self = this
var title = ''
this.sku_pro = {}
this.pro_info.pro_sku.map(function(item, elem) {
if(self.pro_code == item.sku_code){
self.sku_pro = item
title = item.sku_name
console.log(self.sku_pro)
}
})
return ''
}
},
methods: {
getNearShop: function(){
var self = this
self.getLocation(false).then(function(res){

self.lat = res.latitude
self.lng = res.longitude





})
},
tuijian: function(){
this.$refs.share.showBox = true
},
fetchComment: function () {
let self = this
self.xarpost('Comment/getCommentList', {limit: 3}).then((d) => {
let {data, error, message} = d
self.commentInfo = data
})
},
buyVcard: function(){
var self = this
if(this.pro_code){
var proinfo = []
proinfo.push({
sku_id: this.sku_pro.sku_id,
pro_id: this.pro_id,
pro_name: this.sku_pro.sku_name,
pro_quantity: 1
})
console.log(JSON.stringify(proinfo))
this.xarpay(this.pro_info.pro_name, this.sku_pro.sku_name, this.pro_id, JSON.stringify(proinfo)).then(function(){
self.$router.push({
'path': '/buySuccess?sharePath=/vcard/2&title=&description='
})
}, function(){
console.log('aaaa')
})
}else{
this.showtoast('')
}
},
allSkucode: function() {
var self = this
var tmp_sku_code = 'P' + self.pro_id + 'P'
var sku_list = [tmp_sku_code]
this.pro_info.property.map(function(item, elem) {
var tmp = []
item.value.map(function(item1, elem1) {
sku_list.map(function(item2, elem2) {
tmp.push(item2+item1.pv_id)
})
})
sku_list = tmp
})
this.sku_code_list = sku_list
},
selectpv: function (index, index1, i) {
var self = this
console.log(this.createSkuCode)
this.pro_info.property[index].value.map(function(i, elem) {

if(index1 == elem){
i.select = !i.select;
self.pro_info.property[index].is_select = i.select
self.pro_info.property[index].s_index = elem

}else{
i.select = 0;
}
})
}
}

}



